import NewsletterStyleOne from "../components/NewsletterStyleOne";
import Testimonial from "../components/Testimonial";
import ServicePolicy from "../components/policy/ServicePolicy";

export default {
  components: {
    NewsletterStyleOne,
    ServicePolicy,
    Testimonial,
    HeaderWithTopbar: () => import("@/components/TheHeader"),
    Breadcrumb: () => import("@/components/Breadcrumb"),
    TheFooter: () => import("@/components/TheFooter"),
  },
  data() {
    return {
      phones: [
        {
          id: 1,
          tel: "021-72418400",
          href: "2172418400",
        },
        {
          id: 2,
          tel: "021-72418401",
          href: "2172418401",
        },
        {
          id: 3,
          tel: "021-72418402",
          href: "2172418402",
        },
        {
          id: 4,
          tel: "021-72418403",
          href: "2172418403",
        },
        {
          id: 5,
          tel: "021-72418404",
          href: "2172418404",
        },
        {
          id: 6,
          tel: "021-72418405",
          href: "2172418405",
        },
        {
          id: 7,
          tel: "021-72418406",
          href: "2172418406",
        },
      ],
      whatsapps: [
        {
          id: 1,
          tel: "09190808507",
          href: "989190808507",
        },
        {
          id: 2,
          tel: "09902121045",
          href: "989902121045",
        },
        {
          id: 3,
          tel: "09902121046",
          href: "989902121046",
        },
        {
          id: 4,
          tel: "09902121047",
          href: "989902121047",
        },
        {
          id: 5,
          tel: "09902121048",
          href: "989902121048",
        },
      ],
      data: {
        phone_number: "",
        full_name: "",
      },
      saving: false,
    };
  },
  mounted() {},
  head() {
    return {
      title: "کمپین سفره یکبار مصرف",
    };
  },
  methods: {
    async sendForm(e) {
      this.saving = true;
      e.preventDefault();
      if (this.data.phone_number.trim() === "") {
        this.saving = false;
        return this.$notify({
          title: "خطا!",
          text: "لطفا شماره تماس خود را وارد کنید.",
          type: "error",
        });
      }
      if (this.data.full_name.trim() === "") {
        this.saving = false;
        return this.$notify({
          title: "خطا!",
          text: "لطفا نام و نام خانوادگی خود را وارد کنید.",
          type: "error",
        });
      }

      const campaign = await this.$axios.post("/campaign-one/form", this.data);
      if (campaign.status === 200) {
        this.$notify({
          title: "عملیات ناموفق!",
          text: campaign.data.message,
          type: "error",
        });
      }
      if (campaign.status === 201) {
        return this.$notify({
          title: "عملیات موفق!",
          text: campaign.data.message,
          type: "success",
        });
      }
      this.saving = false;
    },
  },
};
